:root {
    --background-primary: #0a192f;
    --foreground-primary: #ccd6f6;
    --foreground-secondary: #8892b0;
    --foreground-tertiary: #64ffda;

    --font-family-sans: "Calibre","Inter","San Francisco","SF Pro Text",-apple-system,system-ui,sans-serif;
    --font-family-mono: "SF Mono","Fira Code","Fira Mono","Roboto Mono",monospace;

    --fz-xxs: 12px;
    --fz-xs: 13px;
    --fz-sm: 14px;
    --fz-md: 16px;
    --fz-lg: 18px;
    --fz-xl: 20px;
    --fz-xxl: 22px;
    --fz-heading: 32px;
    --fz-icon: 22px;

    --fw-xt: 100;
    --fw-th: 200;
    --fw-normal: 400;
    --fw-bg: 600;
    --fw-xb: 700;

    --navbar-height: 100px;

    --transition: 0.3s;
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    width: 100%;
    min-height: 100%;

    overflow-x: hidden;
    -webkit-font-smoothing: antialiased;

    background-color: var(--background-primary);
    color: var(--foreground-secondary);
    font-family: var(--font-family-sans);

    scroll-behavior: smooth;
}