.social-overlay {
    z-index: 10;
    position: absolute;
    bottom: 0;
    right: 50px;
    width: 20px;
}

.social-overlay .social-overlay-list {
    list-style: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
}

.social-overlay .social-overlay-list .social-overlay-item {
    padding: 10px;
}

.social-overlay .social-overlay-list .social-overlay-item .social-overlay-item-icon-holder {
    color: var(--foreground-secondary);
    text-decoration: none;
}

.social-overlay .social-overlay-list .social-overlay-item .social-overlay-item-icon-holder .social-overlay-item-icon {
    font-size: var(--fz-icon);
    transition: var(--transition);
}

.social-overlay .social-overlay-list .social-overlay-item .social-overlay-item-icon-holder .social-overlay-item-icon:hover {
    color: var(--foreground-tertiary);
}

.social-overlay .social-overlay-list .social-overlay-item:last-of-type {
    margin-bottom: 20px;
}

.social-overlay .social-overlay-list::after {
    content: "";
    width: 1px;
    height: 100px;
    background-color: var(--foreground-secondary);
}